@import url('https://fonts.googleapis.com/css2?family=Allura&display=swap');
html {
    box-sizing: border-box;
    font-size: 100%;
    margin: 0;
    padding: 0;
  }
  
  *, *:before, *:after {
    //box-sizing: inherit;
    margin: 0;
    padding: 0;  
  }
  body {
    font-family: 'Allura', cursive;
    font-size: 30px;
    height: 100vh;
    margin: 0;
    padding: 0;
    line-height: 1.3;
  }  

.flex {
  margin-top: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.school__background {
  z-index: 1;
  width: 600px;
  height: 338px;
  background:url("img/background.png") no-repeat center;
}
.school__blackboard {
  width: 469px;
  height: 246px;
  z-index: 2;
  background:url("img/blackboard.png") repeat-y;
  background-size: 469px 346px;
}
.flex-col {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  width: 100%;
  height: 100%;
}
.school__wood {
  position: relative;
  width: 480px;
  left:-1%;
  top: -4%;
  min-height: 270px;
  z-index: 5;
  background:url("img/wood_border.png") no-repeat center;

}
.black-reg {
  position: relative;
  background:url("img/blackboard.png") no-repeat center;
  width: 469px;
  min-height: 280px;
  background-size: 500px 370px;
  top:0;
  left:0;
  z-index: 10;
}
.reg {
  position: relative;
  background:url("img/wood_border.png") no-repeat;
  width: 499px;
  min-height: 270px;
  top:0;
  left:0;
  background-origin: border-box;
  background-size: 499px 505px;

  z-index: 100;
}
.reg-style {

  input[type=image] {
    margin-right: 47px!important;
    margin-top: 0!important;
}
}
.school__form {
margin: 0;
padding: 23px;
color: aliceblue;

p {
text-align: center;
font-size: 36px;
padding-bottom: 33px;
}
form label{
text-align: right;
display: block;
font-size: 30px;
margin-right: 120px;
}
input[type=image] {
float: right;
border: none;
margin-right: 28px;
margin-top: 15px;
margin-bottom: 10px;
height: 39px;
width: 39px;
}
form input {
color: aliceblue;
height: 28px;
width: 142px;
background: transparent;
margin-left: 30px;
border: 2px solid rgba(255, 255, 255, 0.5);
border-radius: 5px;
}

}
.school__rawlplug {
margin-bottom: 2px;
z-index: 2;
width: 380px;
height: 12px;
background:url("img/rawlplug.png") no-repeat center;
}
.school__chalk {
width: 18px;
height: 7px;
position: absolute;
left:88%;
top: 90%;
z-index: 5;
background:url("img/chalk.png") no-repeat;
}
.error {
padding: 20px 0 0 100px;
font-size: 18px;
color: red;
}
.all {
margin: 0;
padding: 0;
padding-top: 100px;
display: flex;
flex-direction: column;
align-content: flex-start;
justify-content: center;
align-items: center;
margin: 0 auto;
}
.black-rawl {
position: relative;
background:url("img/rawlplug.png") no-repeat;
width: 380px;
height: 12px;
top: -15px;
left: 40px;
}
.container {
position: relative;
background:url("img/background.png");
width: 600px;
min-height: 338px ;
background-size: 469px 470px;
z-index: 0;
}

.black-board {
position: relative;
background:url("img/blackboard.png") repeat-y;
width: 469px;
min-height: 280px;
background-size: 500px 370px;
top:-310px;
left:0;
z-index: 10;
}
.wood-board {
margin: 0;
padding: 10px 25px ;
position: relative;
background:url("img/wood_border.png") no-repeat;
width: 499px;
min-height: 300px;
top:-600px;
left:30px;
background-origin: border-box;
background-size: 499px 305px;

z-index: 100;
}

.wood-mel {
background:url("img/chalk.png") no-repeat;
float: right;
margin-right: -20px;
margin-top: 5px;
width: 28px;
height: 17px;
}